:root {
  --backgroundColor: #dddcf6;
  --textColor: #737fe7;
  --textFont: "Carter One";
}

.question_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--backgroundColor);
  box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  padding: 15px;
  width: 75%;
  height: 450px;
  border: 2px var(--textColor) solid;
}

.question_card_title {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.question_card_question {
  display: flex;
  flex: 2;
  justify-content: center;
  align-items: center;
  width: 100%;
  overflow-wrap: break-word;
}

.question_card_content {
  display: flex;
  flex-direction: column;
}

.question_title {
  font-size: x-large;
  text-align: center;
}

.question_index {
  font-size: large;
  text-align: center;
}

.question_text {
  font-size: x-large;
  text-align: center;
  overflow-wrap: break-word;
  word-break: break-word;
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}

@media (max-width: 450px) {
  .question_title {
    font-size: large;
  }

  .question_index {
    font-size: medium;
  }

  .question_text {
    font-size: large;
  }
}
